import React, { Component } from 'react';
import Modal from 'react-modal';
import './Header.scss';
import menu from 'resource/menu.svg';

const mentionList = ['   !', '   ', ' ', '  !', '    .', '    .'];
class Header extends Component {
  modalMention;
  state = {
    id: '',
    password: '',
    confirm: ''
  }
  onInput = (e, domain) => {
    this.setState({
      ...this.state,
      [domain]: e.target.value
    });
  }
  onSubmit = (e, modalType) => {
    e.preventDefault();
    const { id, password, confirm } = this.state;

    if (modalType === 'membership') {
      if (id.length < 5) {
        alert(' 5  .');
        return;
      }
      if (password.length < 5 || confirm.length < 5) {
        alert(' 5  .');
        return;
      }
    } else if (modalType === 'changePwd') {
      if (password.length < 5 || confirm.length < 5) {
        alert(' 5  .');
        return;
      }
    }

    this.props.handleModal(false);
    this.setState({
      id: '',
      password: '',
      confirm: ''
    });

    if (modalType === 'membership') {
      if (password !== confirm) {
        alert('  .');
        return;
      }
      this.props.onResister(id, password)
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.error(err);
        });
    } else if (modalType === 'login') {
      this.props.onLogin(id, password)
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.error(err);
        });
    } else if (modalType === 'changePwd') {
      if (password !== confirm) {
        alert('  .');
        return;
      }
      this.props.onChangePwd(this.props.user, password)
        .then(id => {
          alert(id + '  .');
        })
        .catch(err => {
          console.error(err);
        });
    }
  }
  toggleSmallNav = () => {
    const style = document.getElementById('nav-dropdown-container').style;
    if(style.display === 'none' || !style.display) {
      style.display = 'flex';
      style.opacity = 1;
    } else {
      style.display = 'none';
      style.opacity = 0;
    }
  }
  // Lifecycle
  componentDidMount() {
    Modal.setAppElement('#root');
  }
  shouldComponentUpdate(nextProps, nextState) {
    if(!this.props.modal && nextProps.modal) {
      this.modalMention = mentionList[Math.floor(Math.random()*mentionList.length)];
    }

    return this.props !== nextProps || this.state !== nextState;
  }

  render() {
    const { modal, modalType, handleModal, onChangePage, Navigator, SmallNavigator } = this.props;
    const modalSubject = modalType === 'login' ? '' : modalType === 'membership' ? '' : '';

    const modalStyle = {
      content: {
        width: '20rem',
        height: 'auto',
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        padding: '0',
        margin: '0',
        border: '1px solid #F46569',
        outline: 'none'
      }
    };

    return (
      <header className="template-header">
        <div className="template-header-logo"></div>
        <div className="template-header-subject" onClick={() => { onChangePage(0) }}>Time Manager</div>
        <nav className="template-header-nav">
          {Navigator}
        </nav>
        <nav className="template-header-nav-small" onClick={this.toggleSmallNav}>
          <img src={menu} alt="menu"/>
          {SmallNavigator}
        </nav>
        <Modal isOpen={modal} onRequestClose={() => { handleModal(!modal); this.setState({id: '', password: '', confirm: ''}) }} style={modalStyle} contentLabel="Time Manager">
          <h2 className="modal-subject">{modalSubject}</h2>
          <form className="modal-form" onSubmit={(e) => this.onSubmit(e, modalType)}>
            <p className="modal-comment">{this.modalMention}</p>
            {modalType !== 'changePwd' ? <input className="modal-input" type="text" placeholder="" value={this.state.id} onChange={(e) => { this.onInput(e, 'id'); }}></input> : ''}
            <input className="modal-input" type="password" placeholder="" value={this.state.password} onChange={(e) => { this.onInput(e, 'password'); }}></input>
            {modalType !== 'login' ? <input className="modal-input" type="password" placeholder=" " value={this.state.confirm} onChange={(e) => { this.onInput(e, 'confirm'); }}></input> : ''}
            <button className="modal-button" type="submit">!</button>
          </form>
        </Modal>
      </header>
    );
  }
}

export default Header;